import { UrlEventType, UrlParameter } from '../requester';
import { Nonce } from './nonce';
import {
  DeviceDataType,
  DeviceType,
  IKameleoonData,
  KameleoonData,
} from './types';

/**
 * @class
 * Device - a class for creating an instance for device of a certain type
 * */
export class Device extends Nonce implements IKameleoonData {
  private device: DeviceType;

  /**
   * @param {DeviceType} device - one of the available device types from `DeviceType` enum
   * */
  constructor(device: DeviceType) {
    super();

    this.device = device;
  }

  /**
   * @property url - url for the current data type used for events tracking
   * */
  get url(): string {
    return (
      UrlEventType.StaticData +
      UrlParameter.DeviceType +
      this.device +
      UrlParameter.Nonce +
      this.nonce
    );
  }

  /**
   * @property data - data of the current class instance
   * */
  get data(): DeviceDataType {
    return {
      device: this.device,
      type: KameleoonData.Device,
      nonce: this.nonce,
    };
  }
}
